// import CannonDebugger from 'cannon-es-debugger';
require('aframe-gltf-part-component');
require('aframe-orbit-controls');
import * as CANNON from "cannon-es";
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";
import { EffectComposer } from "three/examples/jsm/postprocessing/EffectComposer";
import { RenderPass } from "three/examples/jsm/postprocessing/RenderPass";
import { UnrealBloomPass } from "three/examples/jsm/postprocessing/UnrealBloomPass";
import { OutputPass } from "three/examples/jsm/postprocessing/OutputPass";
import * as BIG from './big';
import { gsap } from "gsap";

//
document.addEventListener('DOMContentLoaded', function() {
  document.querySelector('a-scene').setAttribute('load-complete', '');
});

// Stats
// const stats = new Stats();
// const statsContainer = document.getElementById('stats-container');
// statsContainer.appendChild(stats.dom);

//3D
AFRAME.registerComponent('load-complete', {
    init: function() {
      //
      let thisURL = new URL(location);
      let Hash
      let controlMode = "NONE";//NONE, TOUCH, KEY
      const menu = document.querySelector('a.menu');
      const nav = document.querySelector('div.nav');
      const canvas = document.querySelector('canvas.a-canvas');
      const mask = document.querySelector('img.mask');
      const works = document.querySelector('div.work_pages');
      const workpage = document.querySelectorAll('div.work_page');
      const carcontrol = document.querySelectorAll('div.car_control');
      const qmimicontrol = document.querySelectorAll('div.qmimi_control');
      // const box = document.querySelectorAll('span.box');
      // const labledata = document.querySelector('div.lable_data');
      // const lablename = document.querySelectorAll('span.lable_name');

      const sceneEl = document.querySelector('#scene');// SCENE
      const cameraEl = document.querySelector('#basic_camera');// CAMERA
      const lightEl = document.querySelector('#basic_light');// LIGHT
      const qmimiEl = document.querySelector('#qmimi');// QMIMI
      const carEl = document.querySelector('#car');// CAR
      let wheelObjects = [document.querySelector('#car_wheel_fr').object3D, document.querySelector('#car_wheel_fl').object3D, document.querySelector('#car_wheel_br').object3D, document.querySelector('#car_wheel_bl').object3D];
      let world, qmimi, car, follow
      //
      let robotEar = new BIG.Ear(document.querySelector('#robot_ear').object3D);
      let airshipEar = new BIG.Ear(document.querySelector('#airship_ear').object3D);
      let ufo = new BIG.UFO(document.querySelectorAll('#ufo'));
      let ferrisWheel = new BIG.FerrisWheel(document.querySelector('#Ferris_wheel').object3D);
      let fwBall = new BIG.FwBall(document.querySelectorAll('#fw_ball'));
      let airShip = new BIG.AirShip(document.querySelector('#airship').object3D);
      let sequinArray = [];//sequin
      for (let i = 0; i < 300; i++) {
        let _sequin = new BIG.Sequin(airShip.object3D);
        sceneEl.object3D.add(_sequin);
        sequinArray.push(_sequin);
      }
      //
      const carBtn = document.querySelector('div.car_btn');
      const carIn = document.querySelector('img.car_in');
      const carOut = document.querySelector('img.car_out');

      

      //QmimiMove
      let qmStartX, qmStartY, qmDownX, qmDownY, qmNowX, qmNowY
      let qmTouchGoX = 0;
      let qmTouchGoY = 0;
      let isQmimiMoveDragging = false;
      let qmimiDraggableElement = document.getElementById("draggable");
      let qmimiParentElement = qmimiDraggableElement.parentElement;
      let qmTouchNumber

      //Car Left Right Move
      let clrStartX, clrStartY, clrDownX, clrDownY, clrNowX, clrNowY
      let clrTouchGoX = 0;
      let clrTouchGoY = 0;
      let isCarLeftRightDragging = false;
      let clrBtn = document.getElementById('draggable2');
      let clrBtnParentElement = clrBtn.parentElement;
      let clrTouchNumber

      //Car Front Back Move
      let cfbStartX, cfbStartY, cfbDownX, cfbDownY, cfbNowX, cfbNowY
      let cfbTouchGoX = 0;
      let cfbTouchGoY = 0;
      let isCarFrontBackDragging = false;
      let cfbBtn = document.getElementById('draggable3');
      let cfbBtnParentElement = cfbBtn.parentElement;
      let cfbTouchNumber

      let QJumpBtn = document.getElementById('jump');
      let CStopBtn = document.getElementById('carStopBtn');

      
      // 
      function disableScroll() {
        // 
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        var scrollLeft = window.pageXOffset || document.documentElement.scrollLeft;

        // 
        window.onscroll = function() {
            window.scrollTo(scrollLeft, scrollTop);
        };
      }
      
      // 
      function enableScroll() {
        window.onscroll = function() {};
      }

      // hdr setting
      new RGBELoader().load("images/bigcity.hdr", function(texture){
        texture.mapping = THREE.EquirectangularReflectionMapping;
        texture.image.data = texture.image.data.map(value => value * 1);
				sceneEl.object3D.environment = texture;
      });

      // Bloom setting
      const renderScene = new RenderPass( sceneEl.object3D, sceneEl.camera );
      const bloomPass = new UnrealBloomPass( new THREE.Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
			bloomPass.threshold = 1;
			bloomPass.strength = 0.33;
			bloomPass.radius = 0;
      const outputPass = new OutputPass();
      const renderer = sceneEl.renderer;
      const render = renderer.render;

      // Composer setting
      const composer = new EffectComposer( renderer );
      composer.addPass( renderScene );
      composer.addPass( bloomPass );
      composer.addPass( outputPass );
      let isDigest = false;
      renderer.render = function () {
        if (isDigest) {
          render.apply(renderer, arguments);
        } else {
          isDigest = true;
          composer.render();
          isDigest = false;
        }
      };

      // let cannonDebugger;
      let driveConstraint;
      // qmimi model load 
      qmimiEl.addEventListener('model-loaded', e => {
        world = new BIG.World();
        qmimi = new BIG.Qmimi(qmimiEl, world);
        car = new BIG.Car(carEl, wheelObjects, qmimi, carBtn, world);
        follow = qmimi;
        //  ( qmimi  car  )
        driveConstraint = new CANNON.PointToPointConstraint(
          qmimi, // 
          new CANNON.Vec3(0, 0, 0), //  qmimi
          car, // 
          new CANNON.Vec3(0, 0, 0) //  car
        );

        //  CANNON DEBUGGER
        // cannonDebugger = new CannonDebugger(sceneEl.object3D, world, {
        //   color:0xff0000
        // });

        updatePosition();
      });

      const carBtnEvent = new CustomEvent('carBtnModeChange',{ bubbles: true, cancelable: true })
      carBtn.addEventListener('carBtnModeChange', function(e) {
        if(qmimi.carBtnMode == "inCar"){
          carBtn.style.display = "block";
          carIn.style.display = "none";
          carOut.style.display = "block";
        }else if(qmimi.carBtnMode == "besideCar"){
          carBtn.style.display = "block";
          carIn.style.display = "block";
          carOut.style.display = "none";
        }else if(qmimi.carBtnMode == "noCar"){
          carBtn.style.display = "none";
          carIn.style.display = "none";
          carOut.style.display = "none";
        }
      });
      
      carIn.addEventListener( 'click', function(){
        goDrive();
      } );
      carOut.addEventListener( 'click', function(){
        goWalk();
      } );

      const carTouchEvent = new CustomEvent('carTouchChange',{ bubbles: true, cancelable: true })
      carBtn.addEventListener("carTouchChange", function(){
        if(car.car_touch){
          if(qmimi.qmimiMode == "CAR"){
            qmimi.carBtnMode = "inCar";
            carBtn.dispatchEvent(carBtnEvent);
          }else{
            qmimi.carBtnMode = "besideCar";
            carBtn.dispatchEvent(carBtnEvent);
          }
        }else{
          qmimi.carBtnMode = "noCar";
          carBtn.dispatchEvent(carBtnEvent);
        }
      });
      

      function goDrive(){
        qmimi.carBtnMode = "inCar";
        carBtn.dispatchEvent(carBtnEvent);
        qmimi.qmimiMode = "CAR";
        follow = car;
        let qPosition = new CANNON.Vec3(car.position.x, car.position.y + 0.1, car.position.z);
        qmimi.position.copy(qPosition);
        world.addConstraint(driveConstraint); // 
      }
      function goWalk(){
        qmimi.carBtnMode = "besideCar";
        carBtn.dispatchEvent(carBtnEvent);
        qmimi.qmimiMode = "QMIMI";
        follow = qmimi;
        world.removeConstraint(driveConstraint); // 
        let qPosition = new CANNON.Vec3(car.position.x, car.position.y + 2, car.position.z);
        qmimi.position.copy(qPosition);
      }

      // KEYDOWN
      document.addEventListener('keydown', function(event) {
        switch (event.key) {
          case 'w':
            follow.moveForward = true;
            break;
          case 's':
            follow.moveBackward = true;
            break;
          case 'a':
            follow.moveLeft = true;
            break;
          case 'd':
            follow.moveRight = true;
            break;
          case ' ':
            if(follow == qmimi){
              if(qmimi.animateNow !== 'Jump'){ qmimi.jump(); }
            }
            break;
          // case 'q':
          //   if(follow == qmimi){
          //     goDrive();
          //   }else if(follow == car){
          //     goWalk();
          //   }
          //   break;
        }
      });

      // KEYUP
      document.addEventListener('keyup', function(event) {
        switch (event.key) {
          case 'w':
            follow.moveForward = false;
            break;
          case 's':
            follow.moveBackward = false;
            break;
          case 'a':
            follow.moveLeft = false;
            break;
          case 'd':
            follow.moveRight = false;
            break;
        }
      });

      // QMIMI 
      // TOUCH
      // QMIMI JUMP BTN
      QJumpBtn.addEventListener('click', (event) => {
        qmimi.jump();
      });
      QJumpBtn.addEventListener('touchstart', (event) => {
        // 
        let touches = event.touches;
        // 
        for(var i=0; i < event.touches.length; i++) {
          var x = event.touches[i].clientX;
          var y = event.touches[i].clientY;
          var element = document.elementFromPoint(x, y);
          if (QJumpBtn.contains(element)) {
              qmimi.jump();
          }
        }
      });

      // CAR STOP BTN
      CStopBtn.addEventListener('touchstart', (event) => {
        // 
        let touches = event.touches;
        // 
        for(var i=0; i < event.touches.length; i++) {
          var x = event.touches[i].clientX;
          var y = event.touches[i].clientY;
          var element = document.elementFromPoint(x, y);
          if (CStopBtn.contains(element)) {
            vehicle.setBrake(1000, 0);
            vehicle.setBrake(1000, 1);
            vehicle.setBrake(1000, 2);
            vehicle.setBrake(1000, 3);
          }
        }
      });
      CStopBtn.addEventListener('touchend', (event) => {
        vehicle.setBrake(0, 0);
        vehicle.setBrake(0, 1);
        vehicle.setBrake(0, 2);
        vehicle.setBrake(0, 3);
      });

      //QmimiMove Start END
      qmimiDraggableElement.addEventListener('touchstart', (event) => {
        controlMode = "TOUCH";
        onQmimiMoveStart(event);
      });
      qmimiDraggableElement.addEventListener('mousedown', (event) => {
        controlMode = "MOUSE";
        onQmimiMoveStart(event);
      });
      function onQmimiMoveStart(event){
        isQmimiMoveDragging = true;
        if(controlMode == "TOUCH"){
          // 
          for(let i=0; i < event.touches.length; i++) {
            let x = event.touches[i].clientX;
            let y = event.touches[i].clientY;
            let element = document.elementFromPoint(x, y);
            if (qmimiDraggableElement.contains(element)) {
              qmStartX = event.touches[i].clientX - qmimiDraggableElement.offsetLeft;
              qmStartY = event.touches[i].clientY - qmimiDraggableElement.offsetTop;
              qmTouchNumber = i;
            }
          }
        }else if(controlMode == "MOUSE"){
          let x = event.clientX;
          let y = event.clientY;
          qmStartX = event.clientX - qmimiDraggableElement.offsetLeft;
          qmStartY = event.clientY - qmimiDraggableElement.offsetTop;
        }
      }
      qmimiDraggableElement.addEventListener('touchend', onQmimiMoveEnd);
      document.addEventListener('mouseup', function(){
        if(isQmimiMoveDragging){
          onQmimiMoveEnd();
        }
      });
      function onQmimiMoveEnd(){
        isQmimiMoveDragging = false;
        controlMode = "NONE";
        let currentX = (qmimiParentElement.offsetWidth - qmimiDraggableElement.offsetWidth) / 2;
        let currentY = (qmimiParentElement.offsetHeight - qmimiDraggableElement.offsetHeight) / 2;
        // 
        qmimiDraggableElement.style.left = `${currentX}px`;
        qmimiDraggableElement.style.top = `${currentY}px`;
        qmTouchNumber = null;
        qmimi.moveBackward = false;
        qmimi.moveForward = false;
        qmimi.moveRight = false;
        qmimi.moveLeft = false;
      }
      qmimiDraggableElement.ondragstart = function() {
        return false;
      };

      //TouchMove
      document.addEventListener('touchmove', (event) => {
        if(isQmimiMoveDragging){
          onQmimiMove(event);
        }
      }, false);
      document.addEventListener('mousemove', (event) => {
        if(isQmimiMoveDragging){
          onQmimiMove(event);
        }
      });
      function onQmimiMove(event){
        //QmimiMove
        if(isQmimiMoveDragging){
          // 
          let currentX, currentY
          if(controlMode == "TOUCH"){
            currentX = event.touches[qmTouchNumber].clientX - qmStartX;
            currentY = event.touches[qmTouchNumber].clientY - qmStartY;
          }else if(controlMode == "MOUSE"){
            currentX = event.clientX - qmStartX;
            currentY = event.clientY - qmStartY;
          }
          // 
          let minX = 0;
          let maxX = qmimiParentElement.offsetWidth - qmimiDraggableElement.offsetWidth;
          let minY = 0;
          let maxY = qmimiParentElement.offsetHeight - qmimiDraggableElement.offsetHeight;
          currentX = Math.min(Math.max(minX, currentX), maxX);
          currentY = Math.min(Math.max(minY, currentY), maxY);
          // 
          qmimiDraggableElement.style.left = `${currentX}px`;
          qmimiDraggableElement.style.top = `${currentY}px`;
          qmNowX = currentX + qmimiDraggableElement.offsetWidth / 2;
          qmNowY = currentY + qmimiDraggableElement.offsetHeight / 2;
          qmDownX = qmimiParentElement.offsetWidth / 2;
          qmDownY = qmimiParentElement.offsetHeight / 2;
          qmTouchGoX = qmNowX - qmDownX;
          qmTouchGoY = qmNowY - qmDownY;
          if(qmTouchGoY > 5){
            qmimi.moveForward = false;
            qmimi.moveBackward = true;
          }else if(qmTouchGoY < -5){
            qmimi.moveForward = true;
            qmimi.moveBackward = false;
          }else{
            qmimi.moveBackward = false;
            qmimi.moveForward = false;
          }
          if(qmTouchGoX > 5){
            qmimi.moveLeft = false;
            qmimi.moveRight = true;
          }else if(qmTouchGoX < -5){
            qmimi.moveLeft = true;
            qmimi.moveRight = false;
          }else{
            qmimi.moveRight = false;
            qmimi.moveLeft = false;
          }
        }
      }

      // Window_Resize
      window.addEventListener( 'resize', onWindowResize );
      function onWindowResize() {
        const _width = window.innerWidth;
				const _height = window.innerHeight;
        // canvas
        if(Hash == '' || Hash == '#' || Hash == '#menu'){
          gsap.to(canvas, { width: _width, height: _height, ease: "power3.inOut", overwrite: true, duration: 1.2 ,onUpdate: function(){
            mask.style.width = canvas.style.width;
            mask.style.height = canvas.style.height;
            composer.setSize( parseInt(canvas.style.width), parseInt(canvas.style.height) );
            sceneEl.camera.aspect = parseInt(canvas.style.width)/parseInt(canvas.style.height);
            sceneEl.camera.updateProjectionMatrix();// camera
          }});
        }else{
          if(screen.orientation.type == "landscape-primary"){
            gsap.to(canvas, { width: _width/2, height: _height, ease: "power3.inOut", overwrite: true, duration: 0.6 ,onUpdate: function(){
              mask.style.width = canvas.style.width;
              mask.style.height = canvas.style.height;
              composer.setSize( parseInt(canvas.style.width), parseInt(canvas.style.height) );
              sceneEl.camera.aspect = parseInt(canvas.style.width)/parseInt(canvas.style.height);
              sceneEl.camera.updateProjectionMatrix();// camera
            }});
          }else if(screen.orientation.type == "portrait-primary"){
            gsap.to(canvas, { width: _width, height: _height/2, ease: "power3.inOut", overwrite: true, duration: 0.6 ,onUpdate: function(){
              mask.style.width = canvas.style.width;
              mask.style.height = canvas.style.height;
              composer.setSize( parseInt(canvas.style.width), parseInt(canvas.style.height) );
              sceneEl.camera.aspect = parseInt(canvas.style.width)/parseInt(canvas.style.height);
              sceneEl.camera.updateProjectionMatrix();// camera
            }});
          }
          disableScroll();
        }

        // if(Hash == '' || Hash == '#' ||  Hash == '#menu'){
        //   if(screen.orientation.type == "landscape-primary"){
        //     gsap.to(labledata, { top: _height * 0.3 + "px", ease: "power3.inOut", overwrite: true, duration: 1 });
        //   }else if(screen.orientation.type == "portrait-primary"){
        //     gsap.to(labledata, { top: _height * 0.3 + "px", ease: "power3.inOut", overwrite: true, duration: 1 });
        //   }
        // }else{
        //   if(screen.orientation.type == "landscape-primary"){
        //     gsap.to(labledata, { top: _height * 0.3 + "px", ease: "power3.inOut", overwrite: true, duration: 1 });
        //   }else if(screen.orientation.type == "portrait-primary"){
        //     gsap.to(labledata, { top: _height * 0.05 + "px", ease: "power3.inOut", overwrite: true, duration: 1 });
        //   }
        // }
				
			}

      
      //  requestAnimationFrame 
      function updatePosition() {
        if(Hash != '#menu'){ // menu3D
          let time = performance.now() / 1000;
          // WORLD 
          world.update(time);
          qmimi.update();
          car.update();
          if(car.position.distanceTo(qmimi.position) < 4){
            if(!car.car_touch){
              car.car_touch = true;
              carBtn.dispatchEvent(carTouchEvent);
            }
          }else{
            if(car.car_touch){
              car.car_touch = false;
              carBtn.dispatchEvent(carTouchEvent);
            }
          }

          // // CAR
          // for (let i = 0; i < vehicle.wheelInfos.length; i++) {
          //   vehicle.updateWheelTransform(i);
          //   let transform = vehicle.wheelInfos[i].worldTransform;
          //   let wheelBody = wheelBodies[i];
          //   wheelBody.position.copy(transform.position);
          //   wheelBody.quaternion.copy(transform.quaternion);
          //   wheelObjects[i].position.copy(transform.position);
          //   wheelObjects[i].quaternion.copy(transform.quaternion);
          // }
          
          let qmimiPosition = new THREE.Vector3(qmimi.position.x, qmimi.position.y-0.8, qmimi.position.z);
          // orbit-controls
          let targetX, targetY, targetZ;
          if(qmimi.qmimiMode == "QMIMI"){
            targetX = qmimi.position.x;
            targetY = qmimi.position.y + 5;
            targetZ = qmimi.position.z;
          }else{
            targetX = car.position.x;
            targetY = car.position.y + 5;
            targetZ = car.position.z;
          }
          cameraEl.setAttribute('orbit-controls', 'target', targetX + ' ' + targetY + ' ' + targetZ);

          // LIGHT
          lightEl.object3D.position.set(targetX - 10, targetY + 10, targetZ + 10);
          
          // STATS
          // stats.update();

          // CannonDebugger
          // cannonDebugger.update();

          // 
          robotEar.update(time);
          airshipEar.update(time);
          ufo.update(time);
          ferrisWheel.update(time);
          fwBall.update(time);
          airShip.update(time);
          // Sequin
          for (let i = 0; i < sequinArray.length; i++) {
            sequinArray[i].update();
          }
        }

        // 
        requestAnimationFrame(updatePosition);
      }

      //  pageChangeEvent
      let page_Number = 0;
      const pageChangeEvent = new CustomEvent('pageChange',{ bubbles: true, cancelable: true })
      works.addEventListener('pageChange', function(e) {
        workpage.forEach(function(data, i){
          if(page_Number == i){
            gsap.to(data, { x: 0, opacity: 1, ease: "power3.inOut", overwrite: true, duration: 1 });
          }else{
            gsap.to(data, { x: 1000, opacity: 0, ease: "power3.inOut", overwrite: true, duration: 1 });
          }
        });
        // box.forEach(function(data, i){
        //   let _width = window.innerWidth;
        //   if(page_Number == i){
        //     gsap.to(data, { width: _width*0.008, ease: "power3.inOut", overwrite: true, duration: 1 });
        //     data.classList.remove("off_bg");
        //     data.classList.add("on_bg");
        //   }else{
        //     gsap.to(data, { width: _width*0.0024, ease: "power3.inOut", overwrite: true, duration: 1 });
        //     data.classList.remove("on_bg");
        //     data.classList.add("off_bg");
        //   }
        // });
        // lablename.forEach(function(data, i){
        //   let _height = window.innerHeight;
        //   if(page_Number == i){
        //     if(screen.orientation.type == "landscape-primary"){
        //       gsap.to(data, { fontSize: _height*0.03, ease: "power3.inOut", overwrite: true, duration: 1 });
        //     }else if(screen.orientation.type == "portrait-primary"){
        //       gsap.to(data, { fontSize: _height*0.02, ease: "power3.inOut", overwrite: true, duration: 1 });
        //     }
            
        //     data.classList.remove("off_lable");
        //     data.classList.add("on_lable");
        //   }else{
        //     gsap.to(data, { fontSize: 0, ease: "power3.inOut", overwrite: true, duration: 1 });
        //     data.classList.remove("on_lable");
        //     data.classList.add("off_lable");
        //   }
        // });
      })

      //HASH CHANGE
      function locationHashChanged(){
        thisURL = new URL(location);
        let _hash = thisURL.hash;
        let hashArray = _hash.split("=");
        Hash = hashArray[0];

        if(Hash == '#graphic_design'){
          page_Number = 1;
        }else if(Hash == '#3d_production'){
          page_Number = 2;
        }else if(Hash == '#web_design'){
          page_Number = 3;
        }else if(Hash == '#webxr'){
          page_Number = 4;
        }else if(Hash == '#stable_diffusion'){
          page_Number = 5;
        }else if(Hash == '#about_us'){
          page_Number = 6;
        }else{
          page_Number = 0;
        }
        //
        works.dispatchEvent(pageChangeEvent);

        if(Hash == '' || Hash == '#' || Hash == '#menu'){
          gsap.to(works, { x: 1000, opacity: 0, ease: "power3.inOut", overwrite: true, duration: 1 });
          gsap.to(menu, { opacity: 1, ease: "power3.inOut", overwrite: true, duration: 1.2 });
        }else{
          gsap.to(works, { x: 0, opacity: 1, ease: "power3.inOut", overwrite: true, duration: 1 });
          gsap.to(menu, { opacity: 0, ease: "power3.inOut", overwrite: true, duration: 1.2 });
        }

        onWindowResize();

        if(Hash == '#menu'){
          gsap.to(nav, { y: 0, ease: "power3.inOut", overwrite: true, duration: 1.5 });
          renderer.setAnimationLoop(null);
        }else{
          gsap.to(nav, { y: -1500, ease: "power3.inOut", overwrite: true, duration: 1 });
          renderer.setAnimationLoop(sceneEl.render);
        }

        if(Hash == '' || Hash == '#'){
          gsap.to(carcontrol, { y: 0, ease: "power3.inOut", overwrite: true, duration: 1 });
          gsap.to(qmimicontrol, { y: 0, ease: "power3.inOut", overwrite: true, duration: 1 });
        }else{
          gsap.to(carcontrol, { y: 3000, ease: "power3.inOut", overwrite: true, duration: 1 });
          gsap.to(qmimicontrol, { y: 3000, ease: "power3.inOut", overwrite: true, duration: 1 });
        }

      }
      window.onhashchange = locationHashChanged;
      locationHashChanged();
      onWindowResize();
      setTimeout(function(){
        onWindowResize();
      },1000);
    }
});


