import * as CANNON from "cannon-es";
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import CannonUtils from "cannon-es/utils/cannonUtils";
// import { THREE } from "aframe";

//
function dtr(degrees){ return degrees * (Math.PI/180); }//
function rtd(radians){ return radians * (180/Math.PI); }//

// World
class World extends CANNON.World{
    constructor(){
        super({ gravity: new CANNON.Vec3(0, -9.8 * 2, 0) });
        // Clock
        this.clock = new THREE.Clock();
        this.timeStep = 1/60;
        this.lastCallTime = performance.now() / 1000;
        // CANNON Material
        this.qmimiMaterial = new CANNON.Material('qmimi');
        this.groundMaterial = new CANNON.Material('ground');
        let groundBody = new CANNON.Body({
            material: this.groundMaterial,
            mass: 0,
            friction: 0.6,
            position: new CANNON.Vec3(0,0,0),
            quaternion: new CANNON.Quaternion(0,0,0),
            type: CANNON.BODY_TYPES.STATIC
        });
        this.groundBody = groundBody;
        const qmimi_ground = new CANNON.ContactMaterial(this.qmimiMaterial, this.groundMaterial, {
            friction: 0.6,
            restitution: 0,
            contactEquationStiffness: 1000
        })
        this.addContactMaterial(qmimi_ground);
        // 
        let gltfLoader = new GLTFLoader();
        gltfLoader.load('models/cannon.glb', function(gltf){
            gltf.scene.traverse( function ( object ) {
                if ( object.isMesh ){ groundBody.addShape(CannonUtils.CreateTrimesh(object.geometry)); }
            });
        });
        this.addBody(groundBody);
    }
    update(time){
        let dt = time - this.lastCallTime;
        this.lastCallTime = time;
        this.step(this.timeStep, dt);
    }
}

// Qmimi
class Qmimi extends CANNON.Body{
    constructor(qmimiEl, world){
        super({
            material: world.qmimiMaterial,
            mass: 1,
            friction: 1,
            angleDamping: 1,
            linearDamping: 0.8,
            position: new CANNON.Vec3(0,2,96),
            type: CANNON.BODY_TYPES.DYNAMIC
        });
        this.carBtnMode = "noCar"; //carBtnMode: inCar, besideCar, noCar
        this.moveForward = false;
        this.moveBackward = false;
        this.moveLeft = false;
        this.moveRight = false;
        this.forceFront = 0;
        this.forceBack = 0;
        this.forceLeft = 0;
        this.forceRight = 0;
        this.forceNumber = 24;
        // 
        this.qmimi_mesh = qmimiEl.components['gltf-model'].model.children[0];
        // 
        this.qmimi_mixer = new THREE.AnimationMixer(this.qmimi_mesh);
        this.qmimi_clips = qmimiEl.components['gltf-model'].model.animations;
        this.standAction;
        this.walkAction;
        this.runAction;
        this.jumpAction;
        this.driveAction;
        this.nowStandWeight = 1;
        this.nowWalkWeight = 0;
        this.nowRunWeight = 0;
        this.nowJumpWeight = 0;
        this.nowDriveWeight = 0;
        this.goStandWeight = 1;
        this.goWalkWeight = 0;
        this.goRunWeight = 0;
        this.goJumpWeight = 0;
        this.goDriveWeight = 0;
        this.mixerUpdateDelta;
        this.qmimi_speed = 0;//QMIMI
        this.animateNow = 'Stand';
        this.qmimiEl = qmimiEl;
        this.qmimiMode = "QMIMI";//QMIMI, CAR

        // QMIMI
        this.home_touch = false;
        this.web_design_touch = false;
        this.webxr_touch = false;
        this.production_3d_touch = false;
        this.graphic_design_touch = false;
        this.ai_touch = false;
        this.about_us_touch = false;

        this.home_point = new CANNON.Vec3(0, 0, 46.5);
        this.web_design_point = new CANNON.Vec3(-80, 0, 45);
        this.webxr_point = new CANNON.Vec3(80, 0, 46.5);
        this.production_3d_point = new CANNON.Vec3(0, 0, 46.5);
        this.graphic_design_point = new CANNON.Vec3(0, 0, 46.5);
        this.ai_point = new CANNON.Vec3(0, 0, 46.5);
        this.about_us_point = new CANNON.Vec3(0, 0, 46.5);

        this.addShape(new CANNON.Sphere(0.8));
        world.addBody(this);
        this.world = world;
        
        // 
        for (let i = 0; i < this.qmimi_clips.length; i++) {
          if(this.qmimi_clips[i].name == 'Run'){ this.runAction = this.qmimi_mixer.clipAction(this.qmimi_clips[i]); }
          else if(this.qmimi_clips[i].name == 'Walk'){ this.walkAction = this.qmimi_mixer.clipAction(this.qmimi_clips[i]); }
          else if(this.qmimi_clips[i].name == 'Stand'){ this.standAction = this.qmimi_mixer.clipAction(this.qmimi_clips[i]); }
          else if(this.qmimi_clips[i].name == 'Jump'){ this.jumpAction = this.qmimi_mixer.clipAction(this.qmimi_clips[i]); }
          else if(this.qmimi_clips[i].name == 'Drive'){ this.driveAction = this.qmimi_mixer.clipAction(this.qmimi_clips[i]); }
        }
        this.standAction.play();
        this.walkAction.play();
        this.runAction.play();
        this.driveAction.play();
    }
    jump(){
        let _qmimi = this;
        if(_qmimi.animateNow !== 'Jump'){
            _qmimi.animateNow = 'Jump';
            setTimeout(function(){
                _qmimi.jumpAction.stop();
                _qmimi.jumpAction.play();
                _qmimi.type = CANNON.Body.DYNAMIC;
                let force = new CANNON.Vec3(0, 1000, 0);
                let point = new CANNON.Vec3(0, 0, 0);
                _qmimi.applyForce(force, point);
            }, 60);
            setTimeout(function(){
                _qmimi.animateNow = 'Run';
            }, 1000);
        }
    }
    update(){
        if(this.position.distanceTo(this.home_point) < 10){ this.home_touch = true; }
        else{ this.home_touch = false; }
        if(this.position.distanceTo(this.web_design_point) < 10){ this.web_design_touch = true; }
        else{ this.web_design_touch = false; }
        if(this.position.distanceTo(this.webxr_point) < 10){ this.webxr_touch = true; }
        else{ this.webxr_touch = false; }
        if(this.position.distanceTo(this.production_3d_point) < 10){ this.production_3d_touch = true; }
        else{ this.production_3d_touch = false; }
        if(this.position.distanceTo(this.graphic_design_point) < 10){ this.graphic_design_touch = true; }
        else{ this.graphic_design_touch = false; }
        if(this.position.distanceTo(this.ai_point) < 10){ this.ai_touch = true; }
        else{ this.ai_touch = false; }
        if(this.position.distanceTo(this.about_us_point) < 10){ this.about_us_touch = true; }
        else{ this.about_us_touch = false; }

        // qmimi
        let qmimiPosition = new THREE.Vector3(this.position.x, this.position.y-0.8, this.position.z);
        this.qmimiEl.object3D.position.copy(qmimiPosition);

        // qmimi
        if(this.qmimiMode == "QMIMI"){
            if(this.moveForward){ this.forceFront = this.forceNumber; }
            else{ this.forceFront = 0; }
            if(this.moveBackward){ this.forceBack = -this.forceNumber; }
            else{ this.forceBack = 0; }
            if(this.moveLeft){ this.forceLeft = this.forceNumber; }
            else{ this.forceLeft = 0; }
            if(this.moveRight){ this.forceRight = -this.forceNumber; }
            else{ this.forceRight = 0; }

            // 
            let goRY = Math.atan2(this.position.x - document.querySelector('#basic_camera').getObject3D('camera').position.x, this.position.z - document.querySelector('#basic_camera').getObject3D('camera').position.z);
            // 
            let euler = new THREE.Euler(0, goRY, 0, 'XYZ');
            let force = new THREE.Vector3( this.forceLeft + this.forceRight, 0 , this.forceFront + this.forceBack );
            force.applyEuler(euler);
            // 
            this.qmimi_speed = Math.sqrt(Math.pow(this.velocity.x, 2) + Math.pow(this.velocity.z, 2));
            if (this) { // BODY
              let transformedForce = new CANNON.Vec3(force.x, force.y, force.z);
              this.applyForce(transformedForce, new CANNON.Vec3());
              if(this.qmimi_speed > 0.03){
                const zeroQuaternion = new THREE.Quaternion(); // 
                //  quaternion 
                this.qmimiEl.object3D.quaternion.set(zeroQuaternion.x, zeroQuaternion.y, zeroQuaternion.z, zeroQuaternion.w);
                let RY = Math.atan2(this.velocity.x, this.velocity.z);
                this.qmimiEl.object3D.rotation.y = RY;
              }
            }
        }else{
            this.qmimiEl.object3D.quaternion.copy(car.object3D.quaternion); // 
        }
        //ANIMATE
        if(this.qmimiMode == "CAR"){
            this.animateNow = "Drive";
          }else{
            if(this.animateNow !== 'Jump'){
              if(this.qmimi_speed < 1){
                // Stand
                if(this.animateNow !== "Stand"){ this.animateNow = "Stand"; }
              }else if(this.qmimi_speed > 3){
                // Run
                if(this.animateNow !== "Run"){ this.animateNow = "Run"; }
              }else{
                // Walk
                if(this.animateNow !== "Walk"){ this.animateNow = "Walk"; }
              }
            }
          }
          
          if(this.animateNow == "Stand"){
            this.goStandWeight = 1;
            this.goWalkWeight = 0;
            this.goRunWeight = 0;
            this.goJumpWeight = 0;
            this.goDriveWeight = 0;
          }else if(this.animateNow == "Walk"){
            this.goStandWeight = 0;
            this.goWalkWeight = 1;
            this.goRunWeight = 0;
            this.goJumpWeight = 0;
            this.goDriveWeight = 0;
          }else if(this.animateNow == "Run"){
            this.goStandWeight = 0;
            this.goWalkWeight = 0;
            this.goRunWeight = 1;
            this.goJumpWeight = 0;
            this.goDriveWeight = 0;
          }else if(this.animateNow == "Jump"){
            this.goStandWeight = 0;
            this.goWalkWeight = 0;
            this.goRunWeight = 0;
            this.goJumpWeight = 1;
            this.goDriveWeight = 0;
          }else if(this.animateNow == "Drive"){
            this.goStandWeight = 0;
            this.goWalkWeight = 0;
            this.goRunWeight = 0;
            this.goJumpWeight = 0;
            this.goDriveWeight = 1;
          }
          // 
          this.mixerUpdateDelta = this.world.clock.getDelta();
          // Update the animation mixer, the stats panel, and render this frame
          this.qmimi_mixer.update( this.mixerUpdateDelta );
  
          if(Math.abs(this.goRunWeight - this.nowRunWeight) < 0.02){ this.nowRunWeight = this.goRunWeight; }
          else{ this.nowRunWeight = this.nowRunWeight + (this.goRunWeight - this.nowRunWeight)/10; }
          if(Math.abs(this.goWalkWeight - this.nowWalkWeight) < 0.02){ this.nowWalkWeight = this.goWalkWeight; }
          else{ this.nowWalkWeight = this.nowWalkWeight + (this.goWalkWeight - this.nowWalkWeight)/10; }
          if(Math.abs(this.goStandWeight - this.nowStandWeight) < 0.02){ this.nowStandWeight = this.goStandWeight; }
          else{ this.nowStandWeight = this.nowStandWeight + (this.goStandWeight - this.nowStandWeight)/10; }
          if(Math.abs(this.goJumpWeight - this.nowJumpWeight) < 0.02){ this.nowJumpWeight = this.goJumpWeight; }
          else{ this.nowJumpWeight = this.nowJumpWeight + (this.goJumpWeight - this.nowJumpWeight)/5; }
          if(Math.abs(this.goDriveWeight - this.nowDriveWeight) < 0.02){ this.nowDriveWeight = this.goDriveWeight; }
          else{ this.nowDriveWeight = this.nowDriveWeight + (this.goDriveWeight - this.nowDriveWeight)/5; }
          
          this.runAction.setEffectiveWeight(this.nowRunWeight);
          this.walkAction.setEffectiveWeight(this.nowWalkWeight);
          this.standAction.setEffectiveWeight(this.nowStandWeight);
          this.jumpAction.setEffectiveWeight(this.nowJumpWeight);
          this.driveAction.setEffectiveWeight(this.nowDriveWeight);
    }
}

// Car
class Car extends CANNON.Body{
    constructor(carEl, wheelObjects, qmimi, carBtn, world){
        super({
            mass: 180,
            position: new CANNON.Vec3(8,2,93.5)
        });
        this.carBtn = carBtn;
        this.addShape(new CANNON.Sphere(1.2), new CANNON.Vec3(0.3, 2.0, 0));
        this.addShape(new CANNON.Sphere(1.2), new CANNON.Vec3(0.8, 0.2, 0));
        this.addShape(new CANNON.Sphere(1.2), new CANNON.Vec3(-0.9, 0.2, 0));
        this.vehicle = new CANNON.RaycastVehicle({
            chassisBody: this
        });
        
        this.qmimi = qmimi;
        this._moveForward = false;
        this._moveBackward = false;
        this._moveLeft = false;
        this._moveRight = false;
        this.basicForce = 800;
        this.basicSteerVal = 0.8;
        this.forceFront = 0;
        this.forceBack = 0;
        this.steerValLeft = 0;
        this.steerValRight = 0;
        this.brakeForce = 0;
        this.carEl = carEl;
        this.wheelObjects = wheelObjects;
        this.maxSteerVal = 0.8;
        this.maxForce = 1000;
        this.car_touch = false;
        // WHEEL SETTING
        this.wheelBodies = [];
        let wheelOptions = {
            radius: 0.4,
            directionLocal: new CANNON.Vec3(0, -1, 0),
            suspensionStiffness: 30,
            suspensionRestLength: 0.3,
            frictionSlip: 1.4,
            dampingRelaxation: 2.3,
            dampingCompression: 4.4,
            maxSuspensionForce: 100000,
            rollInfluence: 0.01,
            axleLocal: new CANNON.Vec3(0, 0, 1),
            chassisConnectionPointLocal: new CANNON.Vec3(-1, 0, 1),
            maxSuspensionTravel: 0.3,
            customSlidingRotationalSpeed: -30,
            useCustomSlidingRotationalSpeed: true,
        }
        let wx = 1.3;
        let wy = -0.6;
        let wz = 0.9;
        wheelOptions.chassisConnectionPointLocal.set(-wx, wy, wz);
        this.vehicle.addWheel(wheelOptions);
        wheelOptions.chassisConnectionPointLocal.set(-wx, wy, -wz);
        this.vehicle.addWheel(wheelOptions);
        wheelOptions.chassisConnectionPointLocal.set(wx, wy, wz);
        this.vehicle.addWheel(wheelOptions);
        wheelOptions.chassisConnectionPointLocal.set(wx, wy, -wz);
        this.vehicle.addWheel(wheelOptions);
        this.vehicle.addToWorld(world);
        this.vehicle.wheelInfos.forEach((wheel) => {
            let cylinderShape = new CANNON.Cylinder(wheel.radius, wheel.radius, wheel.radius, 18);
            let wheelBody = new CANNON.Body({
                mass: 0,
                material: world.qmimiMaterial,
            });
            wheelBody.type = CANNON.Body.KINEMATIC;
            wheelBody.collisionFilterGroup = 0; // turn off collisions
            let wheel_quaternion = new CANNON.Quaternion().setFromEuler(-Math.PI / 2, 0, 0);
            wheelBody.addShape(cylinderShape, new CANNON.Vec3(), wheel_quaternion);
            this.wheelBodies.push(wheelBody);
            world.addBody(wheelBody);
        })
    }

    get moveForward(){ return this._moveForward; }
    set moveForward(data){
        this._moveForward = data;
        if (this.moveForward) {this.forceFront = -this.basicForce;}
        else{this.forceFront = 0;}
    }
    get moveBackward(){ return this._moveBackward; }
    set moveBackward(data){
        this._moveBackward = data;
        if (this.moveBackward) {this.forceBack = this.basicForce;}
        else{this.forceBack = 0;}
    }
    get moveLeft(){ return this._moveLeft; }
    set moveLeft(data){
        this._moveLeft = data;
        if (this.moveLeft) {this.steerValLeft = this.basicSteerVal;}
        else{this.steerValLeft = 0;}
    }
    get moveRight(){ return this._moveRight; }
    set moveRight(data){
        this._moveRight = data;
        if (this.moveRight) {this.steerValRight = -this.basicSteerVal;}
        else{this.steerValRight = 0;}
    }
    get stop(){ return this._stop; }
    set stop(data){
        this._stop = data;
        if (this.stop){this.brakeForce = 10000;}
        else{this.brakeForce = 0;}
    }
    update(){
        if(this.qmimi.qmimiMode == "CAR"){
            let force = this.forceFront + this.forceBack;
            let steerVal = this.steerValLeft + this.steerValRight;
            //
            let V = Math.floor(rtd(Math.atan2(this.velocity.z, this.velocity.x)));
            if( V < 0 ){ V = 360 + V; }
            let quat = new THREE.Quaternion(this.quaternion.x, this.quaternion.y, this.quaternion.z, this.quaternion.w);
            let rot = new THREE.Euler().setFromQuaternion(quat, "XZY");
            let R = Math.floor(rtd(rot.y));
            if( R < 0 ){ R = 360 + R; }
            let VRTest = Math.abs((360-R)-V);
            if(VRTest > 270){ VRTest = 360 - VRTest; }
            if(VRTest < 90){
                //Front
                this.vehicle.applyEngineForce(force, 0);
                this.vehicle.applyEngineForce(force, 1);
                this.vehicle.applyEngineForce(0, 2);
                this.vehicle.applyEngineForce(0, 3);
                this.vehicle.setBrake(0, 0);
                this.vehicle.setBrake(0, 1);
                this.vehicle.setBrake(this.brakeForce, 2);
                this.vehicle.setBrake(this.brakeForce, 3);
            }else{
                //Back
                this.vehicle.applyEngineForce(0, 0);
                this.vehicle.applyEngineForce(0, 1);
                this.vehicle.applyEngineForce(force, 2);
                this.vehicle.applyEngineForce(force, 3);
                this.vehicle.setBrake(this.brakeForce, 0);
                this.vehicle.setBrake(this.brakeForce, 1);
                this.vehicle.setBrake(0, 2);
                this.vehicle.setBrake(0, 3);
            }
            this.vehicle.setSteeringValue(steerVal, 0);
            this.vehicle.setSteeringValue(steerVal, 1);
        }
        for (let i = 0; i < this.vehicle.wheelInfos.length; i++) {
            this.vehicle.updateWheelTransform(i);
            let transform = this.vehicle.wheelInfos[i].worldTransform;
            let wheelBody = this.wheelBodies[i];
            wheelBody.position.copy(transform.position);
            wheelBody.quaternion.copy(transform.quaternion);
            this.wheelObjects[i].position.copy(transform.position);
            this.wheelObjects[i].quaternion.copy(transform.quaternion);
        }
        this.carEl.object3D.position.copy(this.position);
        this.carEl.object3D.quaternion.copy(this.quaternion);
    }
}

//
class Sequin extends THREE.Mesh{
    constructor(airship){
        let colorArray = [ 0xf9dbc3, 0xed7fa2, 0xe3ffd9, 0xa9d2ff, 0xd2a1ff];
        let rNumber = Math.floor(Math.random()*5);
        let planeGeometry = new THREE.PlaneGeometry(0.3, 0.4, 1, 1);
        let planeMaterial = new THREE.MeshStandardMaterial( {
            roughness: 0,
            metalness: 1,
            emissive: colorArray[rNumber],
            side: THREE.DoubleSide,
        } );
        super(planeGeometry, planeMaterial);
        this.position.x = 40 - Math.random() * 80;
        this.position.y = 40 * Math.random() - 10;
        this.position.z = 40 - Math.random() * 80 + 60;
        this.castShadow = true;
        this.receiveShadow = false;
        let rdx = 0.1 + Math.random() * 0.1;
        let rdy = 0.1 + Math.random() * 0.1;
        let rdz = 0.1 + Math.random() * 0.1;
        this.rotation.x = Math.random() * Math.PI * 2;
        this.rotation.y = Math.random() * Math.PI * 2;
        this.rotation.z = Math.random() * Math.PI * 2;
        this.goX = rdx - Math.random() * rdx * 2;
        this.goY = (- rdy * Math.random() - 0.1) / 5;
        this.goZ = rdz - Math.random() * rdz * 2;
        this.airship = airship;
    }
    update(){
        this.rotation.x += 0.1;
        this.rotation.y += 0.1;
        this.rotation.z += 0.1;
        this.position.x += this.goX - 0.08;
        this.position.y += this.goY;
        this.position.z += this.goZ + 0.08;
        if(this.position.y < -10){
            this.position.x = this.airship.position.x;
            this.position.y = this.airship.position.y;
            this.position.z = this.airship.position.z;
        }
    }
}

//Ear
class Ear {
    constructor(ear){
        this.ear = ear;
    }
    update(time){
        let earRX = Math.sin(time)/3;
        this.ear.rotation.x = earRX;
    }
}

//UFO
class UFO {
    constructor(ufoArray){
        this.ufoArray = ufoArray;
    }
    update(time){
        this.ufoArray.forEach(function(data, i){
            data.object3D.position.x = 80 + 15 * Math.sin(time/2 + i*Math.PI*2/6);
            data.object3D.position.z = 46.5 + 15 * Math.cos(time/2 + i*Math.PI*2/6);
            data.object3D.rotation.y = (Math.atan2(Math.sin(time/2), Math.cos(time/2))+ i*Math.PI*2/6 + Math.PI);
            data.object3D.position.y = 3 + 6 * Math.sin((time/2 + i*Math.PI*2/6)*2)/2;
        });
    }
}

//FerrisWheel
class FerrisWheel {
    constructor(object3D){
        this.object3D = object3D;
    }
    update(time){
        this.object3D.rotation.z = -Math.atan2(Math.sin(time/24), Math.cos(time/24));
    }
}

//FwBall
class FwBall {
    constructor(fwBallArray){
        this.fwBallArray = fwBallArray;
    }
    update(time){
        this.fwBallArray.forEach(function(data, i){
            data.object3D.position.x = 0 + 29 * Math.sin(time/24 + i * Math.PI*2/6);
            data.object3D.position.y = 60 + 29 * Math.cos(time/24 + i * Math.PI*2/6);
            data.object3D.position.z = -133;
        });
    }
}

//AirShip
class AirShip {
    constructor(object3D){
        this.object3D = object3D;
    }
    update(time){
        this.object3D.position.x = 0 + 60 * Math.cos(time/18);
        this.object3D.position.z = 30 + 60 * Math.sin(time/18);
        this.object3D.position.y = 30;
        this.object3D.rotation.y = (Math.atan2(Math.cos(time/18), Math.sin(time/18)) - Math.PI/2);
    }
}

export { World, Qmimi, Car, Sequin, Ear, UFO, FerrisWheel, FwBall, AirShip }